import React from "react"
import { FileRowType } from "@rp/common/utils"
import { EllipsisTooltip } from "@rp/react/web/common/composite-components"
import { FileIcon } from "@rp/react/web/icons"
import { Box } from "@rp/react/web/mui/core"

type PDFViewerHeaderFilename = {
  data: FileRowType | undefined
  name: string
}

function PDFViewerHeaderFilename({ data, name }: PDFViewerHeaderFilename) {
  return (
    <Box
      sx={{
        display: "flex",
        alignItems: "center",
        overflow: "hidden",
      }}
    >
      <FileIcon extension="pdf" />

      <EllipsisTooltip
        tooltipText={data?.name || name}
        typographyProps={{ style: { fontSize: 18, fontWeight: "bold", marginLeft: 12 } }}
      >
        {data?.name || name}
      </EllipsisTooltip>
    </Box>
  )
}

export { PDFViewerHeaderFilename }
